<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Assign Ticket') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <form ref="AssignedTickets" id="AssignedTickets">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="row">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                        <div class="col-md-12 p-0 float-left" id="divAttForm">
                                            <div
                                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
                                                <div
                                                    class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left py-2 p-0">
                                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                        <div class="row mt-0">
                                                            <div class="col-md-6 col-sm-12 float-left mb-2">
                                                                <div class="form-group">
                                                                    <input type="search" v-model="SearchConditionByName"
                                                                        v-on:keypress.enter.prevent="onEnter($event)"
                                                                        class="form-control" placeholder="Search by name" />
                                                                </div>
                                                            </div>
                                                            <div class="col-md-6 col-sm-12 float-left">
                                                                <div
                                                                    class="background-0 text-left form-check-inline search-btm-btn">
                                                                    <a title="Search" v-on:click="searchFunction($event)"
                                                                        href="javascript:;"
                                                                        class="btn btn-success mr-1 px-4"><i
                                                                            class="fa fa-search"></i></a>
                                                                    <a href="javascript:;" title="Clear"
                                                                        v-on:click="refreshUserList()"
                                                                        class="btn btn-danger clearsearchtext px-4"><i
                                                                            class="fa fa-refresh"></i></a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-md-12 p-0">
                                                <div class="listing">
                                                    <div class="table-responsive table-fix-header" style="max-height:300px">
                                                        <table class="table table-bordered dt-responsive nowrap mt-0"
                                                            id="tblUserList">
                                                            <thead class="thead-bg">
                                                                <tr>
                                                                    <th class="ui-resizable">
                                                                    </th>
                                                                    <th class="ui-resizable">
                                                                        <span href="javascript:;" id="NAME">Name</span>
                                                                    </th>

                                                                    <th class="ui-resizable" v-if="isUSIC == false">
                                                                        <span href="javascript:;" id="NAME">Resource
                                                                            Type</span>
                                                                    </th>
                                                                    <th class="ui-resizable">
                                                                        <span href="javascript:;" id="NAME">Skill</span>
                                                                    </th>
                                                                    <th class="ui-resizable">
                                                                        <span href="javascript:;"
                                                                            id="NAME">Department</span>
                                                                    </th>

                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                <tr v-for="(item, index) in UserListing" :key="index">
                                                                    <td>
                                                                        <div class="custom-control custom-checkbox custom-control-inline">
                                                                            <input type="checkbox"
                                                                                class="chkitems custom-control-input chkUserId"
                                                                                v-model="selected" :value="item.user_id"
                                                                                name="chkItemUser"
                                                                                v-on:click="setSelectedValue(item.user_id)"
                                                                                >
                                                                             <label class="custom-control-label universal-custom-control-label pt-1" for="item.user_id"></label> 
                                                                        </div>
                                                                    </td>
                                                                    <td>
                                                                        <div class="d-flex">
                                                                            <span
                                                                                class="my-image d-none d-lg-block img-lmenu"
                                                                                style="line-height:normal;">
                                                                                <img v-if="item.user_image == null"
                                                                                    src="/Content/images/DefaultImages/avatar-icon.jpg"
                                                                                    alt="Image" border="0">
                                                                                <img v-if="item.user_image != null"
                                                                                    :src="item.user_image" alt="Image"
                                                                                    border="0">
                                                                            </span>
                                                                            <span class="mt-2">{{ item.first_name }}
                                                                                {{ item.last_name }}</span>
                                                                        </div>
                                                                    </td>

                                                                    <td v-if="isUSIC == false" data-toggle="tooltip"
                                                                        :title="item.resourceTypeName">
                                                                        {{ TruncateStr(item.resourceTypeName, 30) }}</td>

                                                                    <td data-toggle="tooltip" :title="item.skills">
                                                                        {{ TruncateStr(item.skills, 30) }}</td>
                                                                    <td data-toggle="tooltip" :title="item.department">
                                                                        {{ TruncateStr(item.department, 30) }}</td>

                                                                </tr>
                                                                <tr v-if="UserListing == null || UserListing.length == 0">
                                                                    <td class="text-center text-danger no-record"
                                                                        colspan="7">
                                                                        NoRecordfound
                                                                    </td>
                                                                </tr>
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>

                    </div>
                    <div class="modal-footer">
                        <a href="javascript:;" class="btn btn-primary save-btn mr-2" v-on:click="AssignTicketAction()"
                            title="@Resources.Save"><i class="fa fa-floppy-o pr-2"></i>Save</a>
                        <a href="javascript:;" class="btn btn-primary delete-btn" v-on:click="Close()"
                            title="@Resources.Cancel"><i class="fa fa-times pr-2"></i>Cancel</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ticketids: null,

    },
    data() {
        return {
            isLoading: false,
            UserListing: [],
            SearchConditionByName: '',
            selected: [],
            isUSIC: false,
            //ViewTicketType: this.$route.query.ViewTicketType==""?"":this.$route.query.ViewTicketType,

        }
    },
    created: async function () {
        await this.ShowUserListData();
    },
    methods: {
        ShowUserListData: async function () {
            var vm = this;
            vm.SearchConditionByName == null ? '' : vm.SearchConditionByName
            var params = `search=${vm.SearchConditionByName}`
            await DataServices.GetUserSkillList(params).then(function (response) {
                vm.UserListing = [];
                if (response.data.status == "Success") {
                    vm.UserListing = response.data.result.data;
                }
            });
        },
        Close: function () {
            debugger
            this.$emit('closedialog');
        },
        setSelectedValue: function (val) {
            debugger;
            var vm = this;
            vm.selected = [];
            try {
                if (Array.isArray(val)) {
                    vm.selectvalue = val[0];
                }
                else {
                    vm.selectvalue = val;
                }
            }
            catch (ex) {

            }
        },
        searchFunction: function (e) {
            this.SearchConditionByName = this.SearchConditionByName;
            this.ShowUserListData();
        },
        refreshUserList: function () {
            var vm = this;
            vm.SearchConditionByName = "";
            vm.ShowUserListData();

        },
        AssignTicketAction: function () {
            var vm = this;
            vm.isLoading = true;
            vm.isSubmitted = true;
            var val = '';
            $('#tblUserList tbody input:checkbox:checked').each(function () {
                if (val.length > 0) {
                    val += ",";
                }
                val += $(this).val();
            });
            if (val.length > 0) {
                //     vm.$refs.AssignedTickets.validate().then((result) => {
                //   if (result) {
                vm.isSubmitted = false;
                var selectedUserId = vm.selectvalue;
                var postString = {
                    TicketIds: vm.ticketids,
                    SelectedUserId: selectedUserId,
                    Message: vm.UserListing[0].first_name + ' ' + vm.UserListing[0].last_name,
                    TypeCode: 'TICKET_ASSIGN'
                };
                var objectPostString = JSON.stringify(postString);


                DataServices.TicketingAssignTickets(objectPostString).then((response) => {
                    if (response.data != null) {
                        if (response.data.code == "1") {
                            vm.$parent.fetchRecords();
                            vm.$parent.ShowAssignTicket = false;
                            $("input:not(.chkAll):checkbox:checked").prop('checked', false);
                            vm.ShowAlert(vm.$t('Ticket Assigned Successfully'), "success", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }
                        else {
                            vm.ShowAlert(vm.$t('Assign Ticket failed'), "failure", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }
                    }

                });
                //       }
                //       else {
                //           vm.ShowAlert(vm.$t('Select any record to Assign Ticket'), "warning", true, vm.$t("Alert"));
                //vm.isLoading=false;
                //       }
                // });
            }
            else {
                vm.ShowAlert(vm.$t('Select any record to Assign Ticket'), "warning", true, vm.$t("Alert"));
                vm.isLoading = false;
            }
        },

    }

}
</script>