    async function  bindViewHistoryleftCommonSearchdropdown(instance, moduleName, filtername,data,AllCount) {       
        //debugger;
        var vm = instance;
        var param = "moduleName='"+moduleName+"'&type="+filtername+""
        var param1 = "moduleName=Ticketing&type="+filtername+""
        if(filtername=='FSM_USERS'){
            debugger;
            await DataServices.GetDDLData(param1).then((response) => {                
                response.data.DATA.forEach(element => {
                    let obj = {
                        name: element.name,
                        value: element.value
                    }
                    vm.leftSearchFields[2].listOptions.push(obj);
                });
            });
        }
    }
    async function  bindleftCommonSearchdropdown(instance, moduleName, filtername,data,AllCount) {       
        //debugger;
        var vm = instance;
    var param = "moduleName='"+moduleName+"'&type="+filtername+""
    var param1 = "moduleName=Ticketing&type="+filtername+""
      if (filtername == 'Priority') {
        vm.leftSearchFields[0].listOptions.push({
            name: 'All('+AllCount+')',
            value:''
        });
        data.forEach(element => {
                let obj = {
                    name: element.mapping_name+'('+element.tab_count_data+')',
                    value: element.tab_mapping_id
                }
                vm.leftSearchFields[0].listOptions.push(obj);
            });
    }    
    else if(filtername=='FSM_TICKET_TYPE'){
        //debugger;
        await DataServices.GetDDLData(param1).then((response) => {
            
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[3].listOptions.push(obj);
            });
        });
    }
    else if(filtername=='FSM_USERS'){
        debugger;
        await DataServices.GetDDLData(param1).then((response) => {
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[4].listOptions.push(obj);
            });
        });
    }
    // else if(filtername=='FSM_TICKETING_STATUS'){
    //     debugger;
    //     var stausParams="statusType=Ticketing"
    //     await DataServices.GetFsmStatusDDLData(stausParams).then((response) => {
    //         response.data.forEach(element => {
    //             let obj = {
    //                 name: element.STATUS,
    //                 value: element.STATUS
    //             }
    //             vm.leftSearchFields[5].listOptions.push(obj);
    //         });
    //     });
    // }
    else if(filtername=='Ticketing'){
        var stausParams="statusType=Ticketing"
        await DataServices.GetFsmStatusDDLData(stausParams).then((response) => {
            vm.leftSearchFields[5].listOptions=[];
            response.data.forEach(element => {
                let obj = {
                    name: element.STATUS,
                    value: element.STATUS
                }
                vm.leftSearchFields[5].listOptions.push(obj);
            });
        });
    }
    else if(filtername=='TICKET_SUPERVISOR'){
        //debugger;
        await DataServices.GetDDLData(param1).then((response) => {
            if(response.data && response.data.hasOwnProperty('DATA') ){
                vm.leftSearchFields[6].listOptions.push({
                    name:'All',value:'All'
                })
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[6].listOptions.push(obj);
            });
        }
        });
    }
    else if(filtername=='Critical Approval'){
        let obj = {
            name: 'ALL',
            value: 'ALL'
        }
        let obj2 = {
            name: 'Approval Pending',
            value: 'APPROVE_PENDING'
        }
        let obj3 = {
            name: 'Approved',
            value: 'APPROVED'
        }
        vm.leftSearchFields[7].listOptions.push(obj);
        vm.leftSearchFields[7].listOptions.push(obj2);
        vm.leftSearchFields[7].listOptions.push(obj3); 
    }
    else if(filtername=='Form Submit'){
        let obj = {
            name: 'Yes',
            value: 0
        }
        let obj2 = {
            name: 'No',
            value: 1
        }
        vm.leftSearchFields[8].listOptions.push(obj);
        vm.leftSearchFields[8].listOptions.push(obj2);
    }
    else if(filtername=='TAG_DATA'){
        await DataServices.GetDDLData(param1).then((response) => {            
            response.data.DATA.forEach(element => {
                let obj = {
                    name: element.name,
                    value: element.value
                }
                vm.leftSearchFields[9].listOptions.push(obj);
            });
        });
    }      
        
    };
    
