

<template>
    <transition name="modal">
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t('QuickView') }}
                            </h5>
                            <button v-on:click="$emit('close')" href="javascript:;" class="close"><em class="fa fa-times"
                                    aria-hidden="true"></em></button>
                        </div>
                        <div class="container-fluid p-3" id="quickViewData" v-cloak>
                            <div class="row">
                                <div class="col-lg-5 mb-2">
                                    <div class="p-2 bg-grey text-center"><b class="font-weight-medium">{{ $t('TicketNumber')
                                    }}:</b> {{ TicketNumber }} </div>
                                </div>
                                <div class="col-lg-5 mb-2" v-if="IsFsmLocating != 'normal'">
                                    <div class="p-2 bg-grey text-center"><b class="font-weight-medium">{{$t('Location')}}:</b> {{TicketLocation}}</div>
                                </div>
                                <div v-else class="col-lg-5 mb-2">
                                    <div class="p-2 bg-grey text-center" v-if="IsFsmLocating != 'normal'"><b class="font-weight-medium">{{ $t('Location') }}:</b> {{ TicketLocation }}</div>
                                </div>
                                <div class="col-lg-2 mb-2">
                                    <div class="p-2 bg-info text-center">                                      
                                        <RouterLink v-if="IsFieldService == 'FSM'"
                                            :to="'/Ticketing/Manage/' + NumericTicketId" :title="$t('ViewDetail')" class="text-white">
                                            <i class="fa fa-eye pr-2"></i>{{ $t('ViewDetail') }}
                                        </RouterLink>
                                        <RouterLink v-else  :to="'/Ticketing/Manage/' + NumericTicketId" :title="$t('ViewDetail')"
                                            class="text-white">
                                            <i class="fa fa-eye pr-2"></i>{{ $t('ViewDetail') }}
                                        </RouterLink>
                                    </div>
                                </div>
                            </div>
                            <div class="col-lg-12 mb-2" v-if="ticketType == 'FSM'">
                                <MapComponent class="row" mapType="MapBox" mapContainer="quickViewMapDiv" :Data="mapObj"
                                    mapWidth="1552px" mapHeight="200px" :key="componentKey"></MapComponent>
                            </div>
                        </div>
                        <div class="accordion custom-accr m-3" id="accordionExample">
                            <div class="card">
                                <div class="card-header" id="headingOneQV">
                                    <a data-toggle="collapse" data-target="#collapseQV" aria-expanded="true"
                                        aria-controls="collapseOn" v-on:click="openTicketDetailTab()">
                                        <i class="fa fa-angle-down rotate-icon mr-1" aria-hidden="true"></i>{{
                                            $t('TicketDetail') }}
                                    </a>
                                </div>
                                <div id="collapseQV" class="collapse" aria-labelledby="headingQV"
                                    data-parent="#accordionExample" style="">
                                    <div class="card-body">
                                        <div class="row px-2"
                                            style="min-height:100px;max-height: 500px; overflow-x: hidden;"
                                            v-for="(group, index) in LeftViewData" :key="index">
                                            <div class="light-greybg w-100 mb-2 font-weight-bold mb-2 p-2">
                                                {{ $t(group.group_name, '') }}
                                            </div>
                                            <p class="col-6" v-for="item in group.LeftViewData">
                                                <template v-if="item.form_field_visibility == 'YES'">
                                                    <i class="fa fa-dot-circle-o text-dark float-left pr-2"
                                                        style="font-size:12pt;"></i>
                                                    <span class="pl-1 font-weight-bold" :data-visible="item.name + ' ' + item.form_field_visibility">{{ $t(item.display_name) }}: </span>
                                                    <span v-html="item.value"></span>
                                                </template>

                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="card" v-if="ShowOneCallSource != ''">
                                <div class="card-header" id="headingOneQV">
                                    <a data-toggle="collapse" data-target="#collapseOneQV" aria-expanded="false"
                                        aria-controls="collapseOne" class="collapsed" v-on:click="openTicketTextTab()">
                                        <i class="fa fa-angle-down rotate-icon" aria-hidden="true"></i>
                                        {{ $t('TicketText') }}
                                    </a>
                                </div>
                                <div id="collapseOneQV" class="collapse" aria-labelledby="headingOneQV"
                                    data-parent="#accordionExample" style="">
                                    <div class="card-body">
                                        <pre v-html="TicketTextDetail" v-if="TicketTextDetail != ''"></pre>
                                        <div class="col-12 text-center text-danger" v-if="TicketTextDetail == ''">
                                            {{ $t('NoRecordfound') }}</div>
                                    </div>
                                </div>
                            </div>

                            <div class="card" v-if="isViewMode == 0">
                                <div class="card-header positionrelative" id="headingTwoQV">
                                    <a class="collapsed" data-toggle="collapse" data-target="#collapseTwoQV"
                                        aria-expanded="false" aria-controls="collapseTwo"
                                        v-on:click="LoadNotesForQuickView('FSM')">
                                        <i class="fa fa-angle-down rotate-icon mr-1" aria-hidden="true"></i> {{
                                            $t('Notes') }}
                                    </a>
                                    <a v-if="isViewMode == 0 && showAddButton"
                                        class="accordian-header-button btn btn-success addnotes"
                                        v-on:click="LoadTicketTabData('addnotes', NumericTicketId, 'FSM')"
                                        data-toggle="collapse" href="#collapseAddNotesData" role="button"
                                        aria-expanded="true" aria-controls="collapseExample4" :title="$t('AddNote')"><i
                                            class="fa fa-plus pr-2"></i>{{ $t('AddNote') }}</a>
                                </div>
                                <div id="collapseTwoQV" class="collapse" aria-labelledby="headingTwoQV"
                                    data-parent="#accordionExample">
                                    <div class="card-body">
                                        <div class="w-100">
                                            <div class="w-100">
                                                <!-- <AddEditNote v-if="isShowAddNote" :HeaderName="$t('AddNotes')"
                                                                                @close="isShowAddNote = false"></AddEditNote> -->

                                                <AddNoteData v-if="isShowAddNote" :id="sourceId" modulename="Ticketing"
                                                    submodulecode="Ticketing" :configdata="ConfigData"
                                                    v-bind:callbackfunction="LoadNotesForQuickView"
                                                    :HeaderName="$t('AddNotes')" :classicEditor="classicEditor" :ckEditor="ckEditor"></AddNoteData>
                                            </div>
                                        </div>
                                        <div class="pb-2" id="div_NotesLabel">
                                            <span class="legend-Progress colorboxsquare"></span>{{ $t('Private') }}
                                            <span class="legend-upcoming colorboxsquare ml-3"></span>{{ $t('Public') }}
                                        </div>
                                        <div class="w-100" id="tab-notes-content-quickview" v-if="NoteList.length > 0">
                                            <TicketNotes :NoteData="NoteList" :ListNoteData="ListNoteData"></TicketNotes>
                                        </div>
                                        <div class="text-danger text-center d-block no-record" v-if="noRecord">
                                            {{ $t('NoRecordfound') }}
                                        </div>
                                    </div>
                                </div>
                            </div>


                            <div class="card" v-if="isViewMode == 0 && UserType != 'NU'">
                                <div class="card-header" id="heading3QV">
                                    <a data-toggle="collapse" data-target="#collapse3QV" aria-expanded="false"
                                        aria-controls="collapseOne" class="collapsed" v-on:click="LoadThreads()">
                                        <i class="fa fa-angle-down rotate-icon mr-1" aria-hidden="true"></i>{{
                                            $t('Activities') }}
                                    </a>
                                </div>
                                <div id="collapse3QV" class="collapse" aria-labelledby="heading3QV"
                                    data-parent="#accordionExample" style="">
                                    <div class="card-body w-100 float-left">
                                        <div class="w-100 activity-tab" id="divconversationbox-quickview"
                                            v-if="ThreadDetail.length > 0">
                                            <ThreadsList :TicketThreads="ThreadDetail"></ThreadsList>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="card" v-if="IsFieldService == 'FSM'">
                                <div class="card-header" id="heading4QV">
                                    <a data-toggle="collapse" data-target="#collapse4QV" aria-expanded="false"
                                        aria-controls="collapseOne" class="collapsed" v-on:click="FetchWorkOrderData()">
                                        <i class="fa fa-angle-down rotate-icon mr-1" aria-hidden="true"></i>
                                        {{ $t('WorkOrder') }}
                                    </a>
                                </div>
                                <div id="collapse4QV" class="collapse" aria-labelledby="heading4QV"
                                    data-parent="#accordionExample" style="">
                                    <div class="card-body">
                                        <div class="w-100" id="tab-workorder-content">
                                            <FsmworkOrder v-bind:workorderlist="WorkOrderList" :sourceIds="sourceId"
                                                submodulecode="Ticketing" modulename="Ticketing" :CompanyType="CompanyType" :callBackFunction="FetchWorkOrderData"></FsmworkOrder>
                                        </div>
                                        <div style="clear:both;"></div>
                                        <div class="w-100" v-show="WoTotalRecords > 10">
                                            <!-- <pager-control v-bind:numberofrecords="WoTotalRecords" prev-text="@Resources.Prev" first-text="@Resources.First" last-text="@Resources.Last" next-text="@Resources.Next" v-bind:callbackfunction="FetchWorkOrderData"></pager-control> -->
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="card">
                                <div class="card-header" id="heading5QV">
                                    <a data-toggle="collapse" data-target="#collapse5QV" aria-expanded="false"
                                        aria-controls="collapseOne" class="collapsed" v-on:click="FetchAttachmentData()">
                                        <i class="fa fa-angle-down rotate-icon mr-1" aria-hidden="true"></i>
                                        {{ $t('Attachments') }}
                                    </a>

                                    <template v-if="showAddButton">
                                        <a class="accordian-header-button btn btn-success float-right mb-1"
                                            v-if="TicketStatus != 'TICKET_CLOSED' && TicketStatus != 'TICKET_CLOSEDLOCKED'"
                                            data-toggle="collapse" v-on:click="openAttachmentModel()"
                                            href="#collapseAddNotesData" role="button" aria-expanded="true"
                                            aria-controls="collapseExample6" title="Add Attachment"><i
                                                class="fa fa-plus pr-2"></i>{{ $t('Add') }} {{ $t('Attachment') }}</a>
                                    </template>
                                </div>
                                <div id="collapse5QV" class="collapse" aria-labelledby="heading5QV"
                                    data-parent="#accordionExample" style="">
                                    <div class="card-body float-left w-100" v-if="showAddButton">
                                        <div id="div_ShowGpxFile" class="col-12 p-0 d-flex flex-wrap"></div>
                                        <div id="divAttachment" class="col-12 p-0 d-flex flex-wrap scroll">

                                            <ShowTicketAttachment v-if="showAttachmentModal"
                                                v-on:close="showAttachmentModal = false" :id="sourceId"
                                                :configdata="ConfigData" v-bind:callbackfunction="FetchAttachmentData"
                                                modulename="Ticketing" submodulecode="Ticketing">
                                                <h5 slot="header">{{ $t('Attachments') }}</h5>
                                            </ShowTicketAttachment>


                                            <div class="listing pt-1" v-if="AttachmentList != null">
                                                <TicketAttachment :AttachmentData="AttachmentList"
                                                    :ListViewAttachment="ListViewAttachment"
                                                    @open-image="openSpecificImage"></TicketAttachment>
                                            </div>

                                            <div class="text-danger text-center no-record float-left w-100" v-if="noRecord">
                                                {{ $t('NoRecordfound') }}
                                            </div>

                                            <!-- <VueEasyLightbox v-bind:visible="visible" v-bind:imgs="galleryImages"
                                                                v-bind:index="index" @@hide="handleHide"></VueEasyLightbox> -->

                                        </div>

                                    </div>
                                </div>
                                <VueEasyLightbox ref="lightbox" :show="true" v-if="visible" v-bind:images="galleryImages"
                                    @close="visible = false" :customClasses="lightboxClasses"></VueEasyLightbox>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- <modaltrackhistory v-if="showTrackHistoryModalList" v-on:close="showTrackHistoryModalList = false, modalTrackHistoryItemList = null">
        <h5 slot="header">{{$t('TrackHistory')}}</h5>
    </modaltrackhistory>
    <modalupdatestatushistory v-if="showUpdateStatusHistoryModal" v-on:close="showUpdateStatusHistoryModal = false, modalUpdateStatusHistoryItem = null">
        <h5 slot="header">{{$t('History')}}</h5>
    </modalupdatestatushistory>
    <modalupdatestatus v-if="showUpdateStatusModal" v-on:close="showUpdateStatusModal = false, modalUpdateStatusItem = null">
                                <h5 slot="header">{{$t('Update')}} {{$t('Status')}}</h5>
                            </modalupdatestatus> -->
    </transition>
</template>

<script>

import DataServices from "@/services/DataService";
import TicketNotes from "./TicketNotes.vue";
import ThreadsList from "./ThreadsList.vue";
import TicketAttachment from "./TicketAttachment.vue";
import MapComponent from "../Common/MapComponent.vue";
import AddNoteData from "../../views/Ticketing/AddNoteData.vue";
import ShowTicketAttachment from "../../views/Ticketing/ShowAttachmentData.vue";
import FsmworkOrder from "../../views/Ticketing/AddFsmWorkOrder.vue";
//import VueEasyLightbox from 'vue-image-lightbox-carousel';
export default {
    components: {
        TicketNotes,
        ThreadsList,
        TicketAttachment,
        MapComponent,
        FsmworkOrder,
        AddNoteData,
        ShowTicketAttachment,
        //VueEasyLightbox
    },
    mounted() {
        // Automatically trigger a click event on the link to expand the content
        const link = document.querySelector('#headingOneQV a');
        if (link) {
            link.click();
        }
    },
    props: {
        classicEditor: null,
        ckEditor: null,
        sourceIds: {
            type: Number,
            required: true
        },
        EncryptTicketId: {
            type: String,
            required: false
        },
        isFileServiceIds: {
            type: String,
            required: false
        },
        isTicketType: {
            type: String,
            required: false
        },
        CompanyType:{
            type: String,
            required: false
        }       
    },
    data() {
        return {
            ListViewAttachment: 0,            
            ListNoteData: 0,
            moduleName: 'FSM',
            isLoading: false,
            IsFsmLocating: '',
            TicketId: '',
            NumericTicketId: '',
            IsSupportTicket: '',
            TicketStatus: '',
            TicketARType: '',
            IsShowAcceptReject: '',
            IsFieldService: '',
            OneCallSource: '',
            IsFSMVIEWMODE: '',
            isShowAddNote: false,
            TicketTextDetail: "",
            WorkOrderList: [],
            noRecord: false,
            WoTotalRecords: 0,
            showUnauthorizedMessage: true,
            TicketNumber: '',
            TicketLocation: '',
            AttachmentList: [],
            galleryFilesView: [],
            galleryImages: [],
            visible: false,
            index: 0,
            showAttachmentModal: false,
            IsListView: false,
            isView: 1,
            isViewMode: 0,
            currentObject: null,
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            GroupStart: 1,
            GroupEnd: 3,
            ConfigData: [],
            showUpdateStatusHistoryModal: false,
            showTrackHistoryModalList: false,
            WorkOrderId: 0,
            showUpdateStatusModal: false,
            WorkOrderTicketIdForEdit: null,
            WorkOrderOneCallCenter: null,
            WorkOrderItemName: null,
            TicketStatus: "TicketStatus",
            ShowOneCallSource: '',
            LeftViewData: [],
            showAddButton: true,
            NoteList: [],
            ThreadDetail: [],
            UserId: null,
            UserType: null,
            mapObj: [],
            componentKey: 0,
            ticketType: null,
            TicketFileSize: 0,
            TicketDisallowedFileTypes: null
        }
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.IsFieldService = vm.isFileServiceIds;
        vm.UserId = vm.GetUserInfo.ID;
        vm.sourceId = vm.sourceIds;
        vm.NumericTicketId = vm.EncryptTicketId;
        vm.ticketType = vm.isTicketType;
        vm.IsFsmLocating = this.CompanyType;
        await vm.fetchTicketData();
    },
    methods: {
        openSpecificImage(attachmentPath, attachmentFileName) {
            debugger;
            var vm = this;
            var obj = {
                path: attachmentPath,
                //caption: "Attachment " + (index1+1),
                caption: attachmentFileName.split('.')[0]
            }
            vm.galleryImages.push(obj);
        },
        openVideoPlayer: function (videoFilePath, videoFileName, videoFileType, gpxFilePath) {
            $('#div_ShowGpxFile').html("");
            $.ajax({
                url: '/FSM/ShowGpxPlayer',
                contentType: 'application/html; charset=utf-8',
                type: 'GET',
                dataType: 'html',
                data: { videoFilePath: videoFilePath, videoFileName: videoFileName, videoFileType: videoFileType, gpxFilePath: gpxFilePath }
            }).success(function (result) {
                $('#div_ShowGpxFile').html(result);
                setTimeout(function () {
                    $("#div_ShowGpxFile .dDataDropZone").prepend("<span class='closePlayer' style='position: absolute;right: -6px;top: -9px;background:#fff;padding:2px;width:25px;height:25px;text-align:center;border-radius:100%; cursor:pointer;'><i class='fa fa-times-circle fa-size text-danger'></i></span>");
                    $("#div_ShowGpxFile .dDataDropZone span.closePlayer").on("click", function () {
                        $('#div_ShowGpxFile').html("");
                    });
                }, 100);

            });
        },
        formatDateTime(value) {
            //
            if (value) {
                try {
                    var timeFormat = Globalize.culture().calendar.patterns.t;
                    timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                    var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
                    return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
                } catch (e) {
                    var timeFormat = Globalize.culture().calendar.patterns.t;
                    timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                    return moment(String(value)).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
                }
            }
        },
        convertToTimeFormatHHMMSS(value) {
            const sec = parseInt(value, 10); // convert value to number if it's string
            let hours = Math.floor(sec / 3600); // get hours
            let minutes = Math.floor((sec - (hours * 3600)) / 60); // get minutes
            let seconds = sec - (hours * 3600) - (minutes * 60); //  get seconds
            // add 0 if value < 10
            if (hours < 10) { hours = "0" + hours; }
            if (minutes < 10) { minutes = "0" + minutes; }
            if (seconds < 10) { seconds = "0" + seconds; }
            return hours + ':' + minutes + ':' + seconds; // Return is HH : MM : SS
        },
        openTicketTextTab: function () {
            var url = `TicketId=${this.sourceId}&formId=`;
            DataServices.GetTicketText(url).then(function (response) {
                //debugger;
                if (response.data != null && response.data.status == "Success") {
                    vueObj.TicketTextDetail = response.data.result.DATA[0].TICKETTEXT;
                }
            });
        },
        handleHide: function () {
            this.visible = false
        },
        openTicketDetailTab: function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var apiUrl = `str_ticket_id=${vueObj.sourceId}&moduleName=Ticketing&subModuleCode=Ticketing`
            DataServices.ViewPageDetails(apiUrl).then(function (response) {
                debugger;
                vueObj.isLoading = false;
                if (response.data.result != null) {
                    var fullLeftJSON = response.data.result.LEFTDATA;
                    vueObj.ConfigData = response.data.result.CONFIG_DATA;
                    fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                        if (vueObj.IsFsmLocating == 'normal' || vueObj.IsFieldService != 'FSM') {
                            if (element.name == 'ticket_subject' || element.name == 'priority_id' || element.name == 'ticket_category_id' || element.name == 'ticket_department_id' || element.name == 'assigned_to' ||
                                element.name == 'status_id' || element.name == 'Description') {
                                element.form_field_visibility = 'YES';
                            } else {
                                element.form_field_visibility = 'NO';
                            }
                        } else {
                            if (element.name == 'ticket_subject' || element.name == 'priority_id' || element.name == 'ticket_category_id' || element.name == 'City' || element.name == 'ticket_department_id' || element.name == 'AddrFrom' || element.name == 'assigned_to' ||
                                element.name == 'status_id' || element.name == 'County' || element.name == 'State' || element.name == 'TypeofWork' || element.name == 'Description' || element.name == 'DigStreet' || element.name == 'Place' || element.name == 'Zip' || element.name == 'Notes') {
                                element.form_field_visibility = 'YES';
                            }
                            else {
                                element.form_field_visibility = 'NO';
                            }
                        }
                        return element.form_field_visibility == 'YES';
                    });
                    if (response.data.result.IS_WORKORDER_COMPLETE == "1")// for status work order complete
                    {
                        vueObj.noStatus = false;
                    } else {
                        vueObj.noStatus = true;
                    }
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        var groupId = fullLeftJSON[0].group_id;
                        $.each(fullLeftJSON, $.proxy(function (index, item) {
                            if (item.name == "ticket_number") {
                                vueObj.TicketNumber = item.value;
                            }
                            if (item.name == "TypeofWork") {
                                vueObj.TicketName = item.value;
                            }
                            groupId = item.group_id;
                            var chkGroup = $.grep(vueObj.LeftViewData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData
                                    };
                                    vueObj.LeftViewData.push(objGroup);
                                }
                            }
                        }, this));
                        console.log(vueObj.LeftViewData);
                    }
                    setTimeout(() => {
                        vueObj.SetResponsiveTab();                       
                        vueObj.RadioButtonBootstrap();
                    }, 500);
                }
                //console.clear();

            });
        },
        FetchWorkOrderData: async function () {
            debugger;
            var vueObj = this;
            // if (GetPrivilege("/FSM/WorkOrderList", true, false)) {
            //     vueObj.showUnauthorizedMessage = true;
            //     showLoader();
            //     // var apiUrl = "@Convert.ToString(config.GetSection("FSMAPIURL").Value)";
            //     var url = `ticketId=${vueObj.ticketid}`;
            //     var finalUrl = apiUrl + "api/FSM/WorkOrderList" + url;
            //     vueObj.$http.get(finalUrl,
            //     {
            //         headers: { 'Authorization': 'bearer @tokenid' }
            //     })
            var url = `ticketId=${vueObj.sourceId}`;
            await DataServices.WorkOrderList(url).then(function (response) {
                // debugger;               
                if (response.data.status == "Success") {
                    vueObj.WorkOrderList = response.data.result.data;
                    //$.each(vueObj.WorkOrderList, function (index, item) {
                    //    item.SUB_STATUS_ID = (item.SUB_STATUS_ID == null) ? "" : item.SUB_STATUS_ID;
                    //    item.CURRENT_STATUS = (item.SUB_STATUS_ID == null) ? "" : item.SUB_STATUS_ID;
                    //    item.TrackHistoryList = [];
                    //    item.AddEditTextTimer = 'Add';
                    //});
                    if (vueObj.WorkOrderList != null && vueObj.WorkOrderList.length > 0) {
                        vueObj.WoTotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.noRecord = false;
                    }
                    else {
                        vueObj.WoTotalRecords = 0;
                        vueObj.noRecord = true;
                    }
                }
                //console.log(vueObj.LeftViewData);
                //vueObj.GetDDLData("FSM_WORK_ORDER");              
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            });


        },
        LoadThreads: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `ticketId=${vm.NumericTicketId}&isFor=${vm.moduleName}&isView=1`;
            await DataServices.GetTicketById(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null) {
                    vm.ThreadDetail = response.data.ticket.ticketThreads;
                    console.log(vm.ThreadDetail)
                }
            });
        },
        getExtension: function (fileName) {
            debugger;
            fileName = String(fileName).toLowerCase();
            var ext = fileName.split(/[\\/]/).pop(),  // extract file name from full path ...
                // (supports `\\` and `/` separators)
                pos = ext.lastIndexOf(".");       // get last position of `.`
            if (ext === "" || pos < 1)            // if file name is empty or ...
                return "";                             //  `.` not found (-1) or comes first (0)
            ext = ext.slice(pos + 1);
            if (ext == "png" || ext == "jpeg" || ext == "jpg" || ext == "gif" || ext == "bmp") {
                ext = 'image';
            }
            else if (ext == "doc" || ext == "docx") {
                ext = 'word';
            }
            else if (ext == "xls" || ext == "xlsx" || ext == "csv") {
                ext = 'excel';
            }
            else if (ext == "mp3" || ext == "wav") {
                ext = 'audio';
            }
            else if (ext == "webm" || ext == "mov" || ext == "mpv" || ext == "mp4") {
                ext = 'movie';
            }
            else if (ext == "pdf") {
                ext = 'pdf';
            }
            else {
                ext = 'txt';
            }
            return ext;            // extract extension ignoring `.`
        },
        showImg: function (index) {
            this.index = index
            this.visible = true
            $('#div_ShowGpxFile').html("");
        },
        handleHide: function () {
            this.visible = false
        },
        downloadAttachment: function (item) {
            $.download('@Url.Action("DownloadFile", "Ticketing", new { area = "" })', "fileId=" + item.TICKET_ATTACHMENT_ID + "&filePath=" + item.FILE_PATH + "&fileType=" + item.ATTACHMENT_DESC + "&fileName=" + item.ATTACHMENT_NAME + "&downloadType=single", "Get");
        },
        openAttachmentModel: function () {
            var vueObj = this;
            vueObj.showAttachmentModal = true;
            $("#divAttachment").scrollTop(0);
        },
        FetchAttachmentData: function () {
            debugger;
            var vueObj = this;
            vueObj.galleryImages = [];
            vueObj.AttachmentList = [];
            vueObj.galleryFilesView = [];
            var url = `ticketId=${vueObj.sourceId}`;
            DataServices.GetTicketFiles(url).then(function (response) {
                if (response.data.status == 'Success') {
                    vueObj.AttachmentList = response.data.result.DATA;
                    vueObj.ListViewAttachment = 1;
                    if (vueObj.AttachmentList != null && vueObj.AttachmentList.length > 0) {
                        vueObj.galleryFilesView = $.grep(vueObj.AttachmentList, function (element, index) {
                            vueObj.noRecord = false;
                            return (JSON.parse(element.attachment_desc).Extension != 'pdf' && JSON.parse(element.attachment_desc).Extension != 'word' && JSON.parse(element.attachment_desc).Extension != 'excel' && JSON.parse(element.attachment_desc).Extension != 'txt');
                        });
                    }
                    else {
                        vueObj.noRecord = true;
                    }
                }
                else {
                    vueObj.AttachmentList = null;
                    vueObj.noRecord = true;
                }
            });
        },


        LoadTicketTabData: function (ctrlClass, ticketId, isFor) {
            var url = "";
            var vueObj = this;
            if (ctrlClass != "") {
                if (ctrlClass == "addnotes") {
                    url = '/Ticketing/EditNote';
                }
                var aData = [];
                aData.push({ name: "ticketId", value: ticketId });
                if (ctrlClass == "addnotes" || ctrlClass == "transfer") {
                    aData.push({ name: "moduleName", value: isFor });
                }
                var objectPostString = JSON.stringify(aData);
                DataServices.EditNote(objectPostString).then((response) => {
                    if (response.TicketStatusId == 1003) {
                        $('#divloadticketdata').empty().html('');
                        ShowAlert(Globalize.localize('SorryThetickethavebeendeleted'), "failure", true, Globalize.localize('Alert'), true);
                    }
                    else {
                        if (isFor == "FSM") {
                            $('#hdnissupttckt').val('@ViewBag.IsSupportTicket');
                            $('#divloadticketdata').removeClass("hide").addClass("show");
                            $("#div_NotesLabel").addClass("d-none");
                            $("#tab-notes-content-quickview").addClass("d-none");
                            vueObj.isShowAddNote = true;
                            $("#divNotes").scrollTop(0);
                        }
                        $('#divloadticketdata').show().empty().html(response.data);
                        $("#divNotes").scrollTop(0);
                    }
                });

            }
        },
        fetchTicketData: function () {
            var vueObj = this;
            vueObj.isLoading = true;
            DataServices.ViewFSMTicketDetails('id=' + vueObj.sourceId).then(function (response) {
                //hideLoader();
                //debugger
                vueObj.mapObj = [];
                var obj = {
                    latlong: response.data.result.LOCATION_NW,
                    refValue: response.data.result.TICKETNUMBER,
                    popupHeaderHtml: '',
                    popupHeaderColor: "",
                    popupBodyHtml: '',
                    popupFooterHtml: '',
                    pinLatLong: {
                        latitude: response.data.result.LOCATION_NW,
                        longitude: response.data.result.LOCATION_NW
                    },
                    polygons: [
                        {
                            name: "",
                            latLong: [{
                                latitude: response.data.result.LOCATION_NW,
                                longitude: response.data.result.LOCATION_NW,
                            }
                            ]
                        }
                    ],
                    address: response.data.result.FORMATED_ADDRESS,
                    priority: '',
                    color_code: ''
                }
                //obj.latLong.push({latitude: "", longitude: ""})
                vueObj.mapObj.push(obj);
                vueObj.isLoading = false;
                if (response.data.result != null) {
                    vueObj.TicketLocation = response.data.result.FORMATED_ADDRESS;
                    vueObj.TicketNumber = response.data.result.TICKETNUMBER;
                    vueObj.TicketStatus = response.data.result.STATUS_CODE;
                }
                if (response.data.result != null && response.data.result.Type == "Google") {
                    //vueObj.TicketLocation = response.data.result.STREET + ' ' + response.data.result.CITY + ' ' + response.data.result.COUNTY + ' ' + response.data.result.STATE;
                    var script = document.createElement("script");
                    script.type = "text/javascript";
                    if (script.readyState) {
                        script.onreadystatechange = function () {
                            if (script.readyState == "loaded" || script.readyState == "complete") {
                                script.onreadystatechange = null;
                                //LoadMapBox("MapBox", response.data.result.MAP_CRED[0].API_KEY, response.data.result);
                            }
                        }
                    } else {
                        //      
                        script.onload = function () {
                            initializeGoolgeMap("Google", response.data.result.API_KEY, response.data.result);
                        }
                    }
                    script.src = "https://maps.googleapis.com/maps/api/js?key=" + response.data.result.API_KEY + "&libraries=places";
                    document.getElementsByTagName("head")[0].appendChild(script)
                }
                else if (response.data.result != null && response.data.result.Type == "MapBox") {
                    var script = document.createElement("script");
                    script.type = "text/javascript";
                    if (script.readyState) {
                        script.onreadystatechange = function () {
                            if (script.readyState == "loaded" || script.readyState == "complete") {
                                script.onreadystatechange = null;
                                //LoadMapBox("MapBox", response.data.result.API_KEY, response.data.result);
                            }
                        }
                    } else {
                        //      
                        script.onload = function () {
                            LoadMapBox("MapBox", response.data.result.API_KEY, response.data.result);
                        }
                    }
                    //LoadMapBox("MapBox", response.data.result.API_KEY, response.data.result);
                    script.src = "//api.mapbox.com/mapbox-gl-js/v1.8.0/mapbox-gl.js";
                    // script.src="//api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-geocoder/v4.4.2/mapbox-gl-geocoder.min.js";
                    document.getElementsByTagName("head")[0].appendChild(script)
                    var script1 = document.createElement('script');
                    script1.type = 'text/javascript';
                    script1.src = "//api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-geocoder/v4.4.2/mapbox-gl-geocoder.min.js";
                    document.getElementsByTagName('head')[0].appendChild(script1);
                }
                else {
                    var script = document.createElement("script");
                    script.type = "text/javascript";
                    if (script.readyState) {
                        script.onreadystatechange = function () {
                            if (script.readyState == "loaded" || script.readyState == "complete") {
                                script.onreadystatechange = null;
                                //LoadMapBox("MapBox", response.data.result.API_KEY, response.data.result);
                            }
                        }
                    } else {
                        //      
                        script.onload = function () {
                            // LoadLeafLet("LeafLet", response.data.result.API_KEY, response.data.result);
                        }
                    }
                    script.src = "//unpkg.com/leaflet@1.6.0/dist/leaflet.js";
                    // script.src="//api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-geocoder/v4.4.2/mapbox-gl-geocoder.min.js";
                    document.getElementsByTagName("head")[0].appendChild(script)
                    var script1 = document.createElement('script');
                    script1.type = 'text/javascript';
                    script1.src = "/Scripts/FsmMaps/dist/leaflet_markerclusterer.js";
                    document.getElementsByTagName('head')[0].appendChild(script1);
                }
            });
        },
        fetchTicketDetail: async function () {
            var vm = this;
            var url = `ticketId=${vm.sourceId}`;
            await DataServices.ViewTicketDetails(url).then(function (response) {
                if (response.data != null && response.data.status == "Success") {
                    vm.ShowOneCallSource = response.data;
                }
                else {
                    vm.noRecord = true;
                }
            });
        },
        LoadNotesForQuickView: async function (moduleName) {
            debugger;
            var vm = this;
            vm.isLoading = true;
            if (moduleName == null || moduleName == 'undefined') {
                moduleName = vm.moduleName;
            }
            var url = `ticketId=${vm.sourceId}&isSuptTckt=false&moduleName=${moduleName}&isView=1`;
            await DataServices.GetWorkOrderNoteList(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null && response.data.length > 0) {
                    vm.NoteList = response.data;
                    vm.ListNoteData = 1;
                    $("#tab-notes-content-quickview").removeClass("d-none");
                }
                else {
                    $("#div_NotesLabel").removeClass("d-none");
                    vm.noRecord = true;
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
            vm.isLoading = false;
        }
    }
}



