<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Notes') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <div class="heading-border">
                            <h5 class="h5"><span>Notes</span><small class="float-right"><a href="javascript:;"
                                        id="showattachment" v-if="isDisabledButton == false" v-on:click="ShowNotePopUp" title="Show/Hide Add">
                                        <i class="fa fa-file-text-o pr-2"></i>{{ $t('Show/Hide') }} {{ $t('Add')}}</a></small></h5>
                        </div>
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="UploadFileWithNote"
                            v-if="ShowNotePopUpData">
                            <template v-slot:tgslot-Description="{ data }">
                                <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"></ckeditor>
                                <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                            </template>
                        </dynamic-form>

                        <div class="w-100" id="tab-notes-content-quickview" v-if="NoteList != null">
                            <TicketNotes :NoteData="NoteList" :ListNoteData="ListNoteData"></TicketNotes>
                        </div>

                        <div class="text-danger text-center no-record float-left w-100" v-if="noRecord">
                            {{ $t('NoRecordfound') }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import TicketNotes from "./TicketNotes.vue";
// import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
//import CKEditor from '@ckeditor/ckeditor5-vue2';
import { Field, getValidationRules } from "@/vee-validate/index"
export default {
    components: {
        TicketNotes,
        //"ckeditor": CKEditor.component
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.ListNoteData = this.ListNoteData;
        this.configData();
        vm.LoadNotesForQuickView('FSM');
    },    
    props: {
        //classicEditor: null,       
        isDisabledButton:{
             type:Boolean,
             required:true
        },
        configdata: {
            type: Array,
            required: true
        },
        id: {
            type: Number,
            required: false
        },
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        },
        ListNoteData:{
            type:Number,
            required:false
        }
    },
    data() {
        return {
            showError: false,
            editor: this.CLASSIC_EDITOR,
            content: "<h1>Some initial content</h1>",
            ticketType: '',
            isLoading: false,
            TicketBindList: [],
            NoteList: [],
            Description: null,
            ShowNotePopUpData: false,
            FileName: null,
            checkFileValidSize: false,
            checkFileValidExt: false,
            allowedPerFileSizeInMb: 0,
            disallowFileTypes: '',
            file: null,
            ticketModel: {
                NoteDescription: '',
                //attachment:'',                
                transferType: '',
                TicketId: 0,
                ModuleName: '',
                SubModuleCode: '',
                NoteId: 0,
                WorkOrderId: 0,
                fileData: []
            },
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('NoteDescription'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 5000 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "RadioField",
                            label: "",
                            name: "Type",
                            value: 2,
                            placeholder: "",
                            validationRules: { "required": false, max: 200 },
                            config: {
                                options: [{ name: 'Private', value: 1 }, { name: 'Public', value: 2 }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },

                        }
                    ],
                },
                {
                    group_name: "Attachment",
                    layoutType: "single",
                    Data: [
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "ListNoteAttachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'doc', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'docx', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'txt', // Text file type
                                        iconClass: 'fa-file-text-o' // Icon class for text files
                                    },
                                    {
                                        fileType: 'jpg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'jpeg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'png', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'gif', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'bmp', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'mp4', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    {
                                        fileType: 'mov', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    // Add more image, document, text, and video types as needed
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: false
                            },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    methods: {
        ShowNotePopUp: function () {
            debugger;
            if (this.ShowNotePopUpData == false) {
                this.ShowNotePopUpData = true;
                this.noRecord = false;
            }
            else {
                this.ShowNotePopUpData = false;
            }
        },
        SizeToMb: function (bytes) {
            // debugger;
            var vueObj = this;
            var sizeInBuytes = parseInt(vueObj.allowedPerFileSizeInMb, 10) * 1048576;
            if (bytes > sizeInBuytes) {
                return -1;
            }
            else {
                var byesInMB = (bytes / 1024) / 1024;
                return byesInMB;
            }
        },
        configData: function (value) {
            debugger;
            var vm = this;
            var data = vm.configdata;
            $.each(data, $.proxy(function (index1, item) {
                if (item.CORE_CONFIG_KEY == "TicketFileSize") {
                    this.allowedPerFileSizeInMb = item.CONFIG_DATA_VALUE;
                }
                else if (item.CORE_CONFIG_KEY == "TicketDisallowedFileTypes") {
                    this.disallowFileTypes = item.CONFIG_DATA_VALUE;
                }
            }, this));

        },
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        setFile: function (e) {
            var vm = this;
            vm.file = e.target.files[0];
            vm.FileName = e.target.files[0].name;
        },
        onChangeRadio: function (e, field) {
            debugger;
            var vm = this;
            vm.ticketType = e.target.dataset.value;
        },
        LoadNotesForQuickView: async function (moduleName) {
            debugger;
            var vm = this;
            if (moduleName == null || moduleName == 'undefined') {
                moduleName = vm.moduleName;
            }
            var url = `ticketId=${vm.id}&isSuptTckt=false&moduleName=${moduleName}&isView=1`;
            await DataServices.GetWorkOrderNoteList(url).then(function (response) {
                if (response.data != null && response.data.length > 0) {
                    vm.NoteList = response.data;
                    $("#tab-notes-content-quickview").removeClass("d-none");
                }
                else {
                    $("#div_NotesLabel").removeClass("d-none");
                    vm.NoteList = null;
                    vm.noRecord = true;
                }
            });
        },
        GetBase64String: function (evt) {
            debugger;
            try {
                // debugger;
                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    debugger;
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;
                        // debugger;
                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },

        UploadFileWithNote: function (formfields, val) {
            debugger;
            var vm = this;
            if (vm._data.FormSchema[0].Data[0].value == '' || vm._data.FormSchema[0].Data[0].value == null) {
                this.showError = true;
            }
            else {
                var extArray = vm.disallowFileTypes.split(',');
                if (extArray.includes(vm.fileExt)) {
                    vm.checkFileValidExt = false;
                    vm.ShowAlert(vm.$t("InvalidFileUploadMsg", vm.fileName, vm.disallowFileTypes), "warning", true, vm.$t("Alert"));
                }
                else {
                    vm.checkFileValidExt = true;
                }

                var fileSizeInMB = this.SizeToMb(vm.fileSize);

                if (fileSizeInMB > parseInt(vm.allowedPerFileSizeInMb, 10)) {
                    vm.checkFileValidSize = false;
                    vm.ShowAlert(vm.$t("ExceedAllowedSize", vm.fileName, vm.allowedPerFileSizeInMb), "warning", true, vm.$t("Alert"));
                }
                else {
                    vm.checkFileValidSize = true;
                }

                if (fileSizeInMB < 0) {
                    vm.checkFileValidSize = false;
                    vm.ShowAlert(vm.$t("ExceedAllowedSize", vm.fileName, vm.allowedPerFileSizeInMb), "warning", true, vm.$t("Alert"));

                } else {
                    vm.checkFileValidSize = true;
                }

                if (vm.checkFileValidExt == true && vm.checkFileValidSize == true) {

                    vm.ticketModel.transferType = vm.ticketType == 1 ? 'Private' : 'Public';
                    vm.ticketModel.NoteDescription = vm._data.FormSchema[0].Data[0].value;
                    vm.ticketModel.TicketId = vm.id == 0 || vm.id == null ? vm.TicketNoteId : vm.id;
                    vm.ticketModel.ModuleName = vm.modulename;
                    vm.ticketModel.SubModuleCode = vm.submodulecode;

                    vm.ticketModel.fileData = vm.fileData;
                    var formData = JSON.stringify(vm.ticketModel);
                    DataServices.AddNote(formData).then((response) => {
                        debugger;
                        try {
                            debugger;
                            if (response.data != null && response.data != "" && response.data.status == "Success") {
                                vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Notes")]), "success", true, vm.$t("Alert"));
                                $("input[type=text]").val('');
                                vm.Close();
                                vm.callbackfunction();

                            }
                            else {
                                // ShowAlert(Globalize.localize('InvalidFileTypMsg'), "failure", true, Globalize.localize("Alert"));
                                vm.FileSrc = '';
                                $("input[type=text]").val('');

                            }
                        } catch (exinner) {
                            return HandleErrorOccured(exinner, 'saveNote');
                        }
                    });
                }

            }
        }, 
        Close: function () {
            debugger;
            this._data.FormSchema[0].Data[0].value = null;
            this.FileName = null;
            this.file = null;
            this.FormSchema[2].Data[0].config.onChange = this.GetBase64String;
            this.$parent.isShowAddNote = false;
            this.$parent.noRecord = false;
        }
    }
}

</script>